.deleteModalContainer {
  left: 0;
  top: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background: rgb(121, 117, 117);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1;
  opacity: 0.94;
}

.deleteModal {
  width: 50%;
  height: 50%;
  position: fixed;
  z-index: 2;
  background: white;
  border: 1px solid black;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
